.test-check,
.edit-pass, .find {
  font-weight: 400;
  padding-left: 10px; }

.hidden {
  display: none; }

.t-search {
  min-width: 300px;
  height: 30px;
  border: 1px solid #ccc;
  padding: 0 25px 0 10px !important;
  font-size: 14px;
  color: #000;
  box-sizing: border-box;
  font-weight: 400;
  background: url(../images/icons/search2.png) 98% 50% no-repeat; }
  .t-search:hover {
    background: url(../images/icons/search2-hover.png) 98% 50% no-repeat; }

.validate-input {
  height: 30px;
  border: 1px solid #ccc;
  padding: 0 25px 0 10px !important;
  font-size: 14px;
  color: #000;
  box-sizing: border-box;
  font-weight: 400;
  background-size: 13px;
  background-position: 98% 50%;
  background-repeat: no-repeat; }
  .validate-input__true {
    background-image: url(../images/icons/valid-true.png); }
  .validate-input__false {
    background-image: url(../images/icons/valid-false.png); }

.timeList {
  padding: 0;
  margin: 0;
  z-index: 10;
  background: #fff;
  position: absolute;
  top: 30px;
  left: 0;
  border: 1px solid #ccc;
  height: 130px;
  overflow-x: hidden;
  box-shadow: 3px 3px 10px #ccc; }
  .timeList__item {
    padding: 5px 30px;
    cursor: pointer; }
    .timeList__item:hover {
      background-color: #dce9f5; }
